<template>
  <div>
    <van-row class="topsearchNav">
      <van-col span="3"  @click="$router.go(-1)">
        <span class="iconfont icon-biaoqing">
          <img src="../assets/img/zuojiantou.png" alt="">
        </span>
      </van-col>
      <van-col span="17">
        <van-search
          v-model="value"
          shape="round"
          background="#F2F2F2"
          placeholder="请输入搜索关键词"
          @input="getserch"
        />
      </van-col>
      <van-col span="4">
        <span class="iconfont icon-sousuo">
          <van-cell is-link @click="getserch">搜索</van-cell>
          <van-popup v-model="show" position="bottom"  :style="{ height: '90%' }" >
            <div class="box" v-for="(item,index) in txtmax" :key="index" @click="getdel(item.goods_id)">
              <span>{{item.goods_name}}</span>
            </div>
            <span v-if="fps">
              抱歉没有搜索到
            </span>
          </van-popup>
        </span>
        
      </van-col>
    </van-row>
    <!-- <div class="hot">
      <div class="remen clearfix">
        <span class="fl">热门搜索</span>
        <span class="fr sapan" @click="btn"><i v-show="spa1">{{cell}}</i><i v-show="spa2">{{val}}</i></span>
      </div>
      <div class="hide" v-show="hiden">
        <ul>
          <li :class="item.class" v-for="(item,index) in list" :key="index">{{item.name}}</li>
        </ul>
      </div>
      <div class="hot-show"  v-show="hides">已隐藏搜索发现</div>
    </div> -->
  </div>
</template>

<script>
export default {
  data(){
    return{
      hiden:true,
      hides:false,
      cell:'隐藏',
      val:'显示',
      spa1:true,
      spa2:false,
      value:'',
      show: false,
      txtmax:'',
      fps:false
    }
  },
  created() {
    //隐藏导航
    this.$store.state.vanTabbar = false;
  },
  // 切换组件生周期钩子，销毁函数
  beforeDestroy() {
    this.$store.state.vanTabbar = true;
  },
  
  methods:{
    btn(){
      this.hiden = !this.hiden;
      this.hides = !this.hides;
      this.spa1 = !this.spa1;
      this.spa2 = !this.spa2;
    },
    getserch(){
      console.log('1111');
      this.show = true;
      this.axios({
        method:'GET',
        url:'/goods/qsearch',
        params:{
          query:this.value
        }
      }).then(result => {
        if(result.status == 200){
          this.txtmax = result.data.message;
          if(this.txtmax == ''){
            this.fps = true
          }
        }
      })
    },
    getdel(aid){
      this.$router.push({ name: "Particulars", query: { aid } });
    },
  }
};
</script>

<style lang="less" scoped>
.box{
  padding: 10px;
  height: 20px;
  line-height: 20px;
  font-size: 16px;
  overflow: hidden;/*超出部分隐藏*/
  white-space: nowrap;/*不换行*/
  text-overflow:ellipsis;/*超出部分省略号显示*/
}
/deep/ .van-cell--clickable{
  height: 100%;
  line-height: 44px;
  font-size: 16px;
  padding: 0;
  background-color: #f2f2f2;
}
/deep/ .van-cell__value--alone{
  text-align: center;
  // line-height: 50px;
  width: 100%;
}
/deep/ .van-cell__right-icon{
  display: none;
}
.topsearchNav {
  background-color: #f2f2f2;
  .icon-biaoqing {
    text-align: center;
    display: block;
    font-size: 24px;
    height: 44px;
    line-height: 44px;
    color: #4e4e4e;
    z-index: 1001;
  }
  .icon-biaoqing img{
    width: 20px;
    height: 20px;
  }
  .van-search {
    padding: 0px;
    margin: 5px;
    background-color: #fff;
    
  }
  .van-search__content {
    background-color: #fff;
    
  }
  .icon-sousuo {
    text-align: center;
    display: block;
    font-size: 24px;
    height: 44px;
    line-height: 44px;
    color: #4e4e4e;
    position: relative;
  }
  .suo{
    width: 40px;
    height: 28px;
    font-size: 14px;
    line-height: 28px;
    text-align: center;
    color: white;
    background-color: #E93B3Ded;
    border-radius: 6px;
    position: absolute;
    top: 50%;
    margin-top: -14px;
  }
}
.hot{
  width: 360px;
  margin: 0 auto;
  .remen{
  width: 330px;
  height: 29px;
  padding: 12px 4px 12px 15px;
  line-height: 29px;
  font-size: 15px;
  }
  .remen .sapan{
    color: #999999;
  }
  .hide{
    width: 100%;
  }
  .hide ul {
    width: 100%;
    height: 100px;
    margin-left: 6px;
    font-size: 14px;
    
  }
  .hide ul .lits{
    color: #E93B3D;
  }
  .hide ul li{
    width: auto;
    overflow: hidden;
    padding: 3px 10px;
    border-radius: 5px;
    line-height: 23px;
    text-align: center;
    margin-right: 6px;
    margin-bottom: 10px;
    height: 23px;
    float: left;
    color: #686868;
    background: #F0F2F5;
    
  }
  .hot-show{
    width: 96%;
    height: 100px;
    text-align: center;
    line-height: 70px;
    color: #999999;
    font-size: 14px;
  }
}
/deep/ .van-overlay{
  background: 0;
}
</style>